// --------------------------------------------
// --------------------------------------------
//
// Pages
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}page--archive {
  // background-color: $bg-color;
  background-color: #fff;
}


.#{$prefix}homepage {
  background-color: $bg-fallback;
  background-image: $bg;
  background-repeat: no-repeat;
  background-attachment: fixed;
}




// --------------------------------------------
// --------------------------------------------
//
// Content wrapper
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}content__wrapper {
  position: relative;
}




// --------------------------------------------
// --------------------------------------------
//
// Backsplash
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}backsplash--page {
  @include clearfix;
  padding: 20px;
  padding-bottom: 6px;
  background-color: $bg-fallback;
  // background-image: $bg;
}




// --------------------------------------------
// --------------------------------------------
//
// Text
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}text {
  @include text;
  font-size: $size-one-quarter;
  margin: 0;
  // color: $primary-text-color;
  color: rgba($primary-text-color, 0.9);
}


.#{$prefix}text--large {
  @include text;
  // font-size: 1.325rem;
  font-size: $size-one-half;
  margin: 0;
  // color: $primary-text-color;
  color: rgba($primary-text-color, 0.9);
}


.#{$prefix}text--small {
  @include text;
  // font-size: 0.875rem;
  font-size: $size-three-fourths;
  margin: 0;
  color: rgba($primary-text-color, 0.9);
}


.#{$prefix}text--bold {
  font-weight: bold;
}


.#{$prefix}slash {
  display: inline-block;
  position: relative;
  top: -2px;
  font-weight: 100;
  font-size: 0.65em;
  margin-left: 8px;
  margin-right: 8px;
}


.#{$prefix}headline--page {
  @include accent;
  margin: 0;
  padding: 40px 20px;
  background-color: #f1f1f1;
  color: rgba($primary-text-color, 0.6);
  font-size: 1.75em;
  font-weight: bold;
  text-transform: capitalize;
  letter-spacing: 1px;
}




// --------------------------------------------
// --------------------------------------------
//
// Homepage
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}headline--homepage {
  @include accent;
  font-size: $size-one-three-fourths;
  color: rgba(255, 255, 255, 0.88);
  letter-spacing: 1px;
}


.#{$prefix}intro--homepage {
  margin: 0;
  font-size: $size-one-quarter;
  font-weight: 300;
  line-height: 1.525;
  color: rgba(255, 255, 255, 0.88);

  @include bp($bp-sm) {
    font-size: $size-one-three-fourths;
    font-weight: 100;
    line-height: 1.325;
  }
}


.#{$prefix}text--homepage {
  margin-top: 18px;
  font-size: $size-one-quarter;
  font-weight: 300;
  line-height: 1.525;
  color: rgba(255, 255, 255, 0.7);

  @include bp($bp-sm) {
    font-size: $size-one-half;
  }
}




// --------------------------------------------
// --------------------------------------------
//
// Link
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}link--hp {
  // @include link(#fff);
  @include link;
}




// --------------------------------------------
// --------------------------------------------
//
// Image
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}img__container {
  padding: 2rem;
  background-color: #f1f1f1;
  border-radius: 4px;
}

.#{$prefix}img {
  border-radius: 4px;
}




// --------------------------------------------
// --------------------------------------------
//
// About
//
// --------------------------------------------
// --------------------------------------------
.#{$prefix}page--about {
  font-size: 1.25rem;
  line-height: 1.4;

  // contextual styling
  .#{$prefix}content__wrapper {
    a { @include link(currentColor); }

  }
}
