.bg-backsplash {
  background-color: $bg-fallback;
  // background-image: linear-gradient(-90deg, #C4174E, #AD84F1);
  background-image: $bg;
  padding: 20px;
  padding-bottom: 25px;
  @include clearfix;
}













.#{$prefix}page__mod {
  @include clearfix;
  width: 100%;
  // max-width: $width-med;
  margin: 0 auto;
  padding: 0 20px;

  @include bp($bp-med) {
    max-width: $width-med;
  }
}

.#{$prefix}page__mod--hp-intro {
  margin-top: 40px;
  transition: margin-top 0.2s ease;

  @include bp($bp-med) {
    margin-top: 6rem;
  }
}